import React, {Component} from 'react';
import PropTypes from 'prop-types';

// images
import scientist from './scientist.jpeg';

// material ui
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import CardMedia from '@material-ui/core/CardMedia';
import Card from '@material-ui/core/Card';

const styles = theme => ({
  root: {
    flexGrow: 1,
    [theme.breakpoints.down('xs')]: {
      padding: "2.5% 10% 7.5%",
    },
    [theme.breakpoints.up('sm')]: {
      padding: "1% 10%",
    },
  },
  form: {
    [theme.breakpoints.down('xs')]: {
      padding: "1% 17.5%",
    },
    [theme.breakpoints.up('sm')]: {
      padding: "2.5% 27.5%",
    },
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  body: {
    padding: "1%",
  },
  button: {
    fontFamily: "Futura",
    margin: "2%",
  },
  buttonBorder: {
    borderColor: '#0062cc',
  },
  bodyText: {
    fontFamily: "Optima",
  },
  headerText: {
    fontFamily: "Optima", 
    color: "#1a1a1a", 
    fontWeight: 800, 
    fontSize: "18px", 
    textAlign: "left",
  },
  wordMark: {
    cursor: "pointer",
  },
  a: {
    color: "#757575",
  },
  formControl: {
    margin: theme.spacing.unit,
  },
  textField: {
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
  }
});

class App extends Component {
  state = {
    showRow2: false,
    showRow3: false,
    selected: [1, 2, 3],
  };

  changeScreenTo = name => event => {
    if (name === 'two') {
      this.setState({ showRow2: true })
    }
    else if (name === 'three') {
      this.setState({ showRow3: true })
    }
  }

  buttonBorder(row, button) {
    return this.state.selected[row] === button ? "borderColor: '#0062cc'" : null;
  }

  render() {
    const { classes } = this.props;

    let row3 = this.state.showRow3 ?
      <div>
        <p style={{ fontFamily: "Optima", color: "#1a1a1a", fontWeight: 600, textAlign: "left" }}>3. How much gray hair do you have?</p>
        <div style={{ lineHeight: "1.6", textAlign: "left" }}>
          <p className={classes.bodyText}>
            When your hair color grows out, how much gray do you see? Base your selection on where you have the most amount of gray (if applicable).
          </p>
        </div>
        <div style={{ padding: "1%" }}></div>
        <Grid item xs={12}>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[2]===1 ? '#0062cc' : null }}>
            Option 1
          </Button>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[2]===2 ? '#0062cc' : null }}>
            Option 2
          </Button>
        </Grid>
        <Grid item xs={12}>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[2]===3 ? '#0062cc' : null }}>
            Option 3
          </Button>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[2]===4 ? '#0062cc' : null }}>
            Option 4
          </Button>
        </Grid>
        <div style={{ padding: "1.5%" }}></div>
      </div>
    : null;

    let row2 = this.state.showRow2 ?
      <div>
        <p style={{ fontFamily: "Optima", color: "#1a1a1a", fontWeight: 600, textAlign: "left" }}>2. How would you describe the texture of your hair?</p>
        <div style={{ lineHeight: "1.6", textAlign: "left" }}>
          <p className={classes.bodyText}>
            Pro tip: Our gentle hair color is safe to use on all hair types.
          </p>
        </div>
        <div style={{ padding: "1%" }}></div>
        <Grid item xs={12}>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[1]===1 ? '#0062cc' : null }} onClick={() => { this.setState({ showRow3: true }) }}>
            Option 1
          </Button>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[1]===2 ? '#0062cc' : null }} onClick={() => { this.setState({ showRow3: true }) }}>
            Option 2
          </Button>
        </Grid>
        <Grid item xs={12}>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[1]===3 ? '#0062cc' : null }} onClick={() => { this.setState({ showRow3: true }) }}>
            Option 3
          </Button>
          <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[1]===4 ? '#0062cc' : null }} onClick={() => { this.setState({ showRow3: true }) }}>
            Option 4
          </Button>
        </Grid>
        <div style={{ padding: "1.5%" }}></div>
      </div>
    : null;

    return (
      <div className={classes.root}>
        <Grid container alignItems="center" justify="center" spacing={40}>
          <Grid item xs={3}>
            <div><p style={{ fontFamily: "Futura", color: "#1a1a1a", textAlign: "center" }} className={classes.wordMark} onClick={this.changeScreenTo('home')}>Conductor</p></div>
          </Grid>
        </Grid>
        <div>
          <Grid container alignItems="center" className={classes.body} spacing={40}>
            <Grid item xs={12} md={6}>
              <Card className={classes.card}>
                <CardMedia
                  component="img"
                  alt="Scientist"
                  className={classes.media}
                  image={scientist}
                  title="Scientist"
                  width="5%"
                />
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper className={classes.paper}>
                <p style={{ fontFamily: "Optima", color: "#1a1a1a", fontWeight: 600, textAlign: "left" }}>1. Let’s start with the end result. What look are you hoping to achieve?</p>
                <div style={{ lineHeight: "1.6", textAlign: "left" }}>
                  <p className={classes.bodyText}>
                    Why we’re asking: The more you can tell us about what you want, the better we can help. And no matter what you select, you’ll be able to view our full range of colors and products.
                  </p>
                </div>
                <div style={{ padding: "1%" }}></div>
                <Grid item xs={12}>
                  <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[0]===1 ? '#0062cc' : null }} value={1} onClick={() => { this.setState({ showRow2: true }) }}>
                    Option 1
                  </Button>
                  <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[0]===2 ? '#0062cc' : null }} value={2} onClick={() => { this.setState({ showRow2: true }) }}>
                    Option 2
                  </Button>
                </Grid>
                <Grid item xs={12}>
                  <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[0]===3 ? '#0062cc' : null }} value={3} onClick={() => { this.setState({ showRow2: true }) }}>
                    Option 3
                  </Button>
                  <Button variant="outlined" className={classes.button} style={{ borderColor: this.state.selected[0]===4 ? '#0062cc' : null }} value={4} onClick={() => { this.setState({ showRow2: true }) }}>
                    Option 4
                  </Button>
                </Grid>
                <div style={{ padding: "1.5%" }}></div>
                {row2}
                {row3}
              </Paper>
            </Grid>
          </Grid>
        </div>
      </div>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);
